"use client";

import React, { useEffect, useState } from 'react';
import { createClient } from '@supabase/supabase-js';
import { Copy, Check, Code } from 'lucide-react';

const supabase = createClient(
  process.env.NEXT_PUBLIC_SUPABASE_URL!,
  process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!
);

export default function InstallPage() {
  const [user, setUser] = useState<any>(null);
  const [copied, setCopied] = useState(false);

  useEffect(() => {
    const getUser = async () => {
      const { data: { user } } = await supabase.auth.getUser();
      setUser(user);
    };
    getUser();
  }, []);

  const embedCode = user ? 
`<script 
  src="https://nora-ai.se/widget.js" 
  data-company-id="${user.id}"
  async
></script>` : 'Laddar...';

  const handleCopy = () => {
    navigator.clipboard.writeText(embedCode);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  return (
    <div className="space-y-8">
      <header>
        <h2 className="text-2xl font-bold text-slate-900">Installera NORA</h2>
        <p className="text-slate-500">Kopiera koden nedan och klistra in den i <code>&lt;head&gt;</code> eller <code>&lt;body&gt;</code> på din hemsida.</p>
      </header>

      <div className="bg-white rounded-xl shadow-sm border border-slate-200 overflow-hidden">
        <div className="bg-slate-900 p-4 flex justify-between items-center border-b border-slate-800">
          <div className="flex items-center gap-2 text-slate-400">
            <Code className="w-4 h-4" />
            <span className="text-xs font-mono">embed-script.html</span>
          </div>
          <button 
            onClick={handleCopy}
            className="text-xs font-medium bg-white/10 hover:bg-white/20 text-white px-3 py-1.5 rounded-lg transition-colors flex items-center gap-2"
          >
            {copied ? <><Check className="w-3 h-3" /> Kopierad</> : <><Copy className="w-3 h-3" /> Kopiera kod</>}
          </button>
        </div>
        <div className="p-6 bg-slate-900">
          <pre className="font-mono text-sm text-green-400 overflow-x-auto whitespace-pre-wrap">
            {embedCode}
          </pre>
        </div>
      </div>

      <div className="grid md:grid-cols-2 gap-6">
        <div className="p-6 bg-slate-50 border border-slate-200 rounded-xl">
          <h3 className="font-semibold text-slate-900 mb-2">Wordpress</h3>
          <p className="text-sm text-slate-600 mb-4">
            Använd ett plugin som "Insert Headers and Footers" för att klistra in koden globalt på din sajt.
          </p>
        </div>
        <div className="p-6 bg-slate-50 border border-slate-200 rounded-xl">
          <h3 className="font-semibold text-slate-900 mb-2">Shopify / Andra CMS</h3>
          <p className="text-sm text-slate-600 mb-4">
            Leta efter filen som heter <code>theme.liquid</code> eller liknande och klistra in koden precis innan <code>&lt;/body&gt;</code>-taggen stängs.
          </p>
        </div>
      </div>
    </div>
  );
}
